<template>
  	<div class="page-loadmore">
	    <div class="page-loadmore-wrapper" ref="wrapper" :style="{ height: wrapperHeight + 'px' }">
	      	<mt-loadmore :top-method="loadTop" @translate-change="translateChange" @top-status-change="handleTopChange" :bottom-method="loadBottom" @bottom-status-change="handleBottomChange" :bottom-all-loaded="allLoaded" ref="loadmore">
		        <ul class="page-loadmore-list" ref="lists">
		          	<li v-for="(item,index) in list" class="page-loadmore-listitem border-bottom"><img :src="item.goodsListImg" alt="" />{{item.className}}</li>
		        </ul>
		       	<div slot="top" class="mint-loadmore-top">
		          	<span v-show="topStatus !== 'loading'" :class="{ 'is-rotate': topStatus === 'drop' }" ref="statusTop">{{statusTop}}↓</span>
		          	<transition name="fade">
			          	<span v-show="topStatus === 'loading'">
			            	<mt-spinner type="snake"></mt-spinner>
			          	</span>
		          	</transition>
		        </div>
		        <div slot="bottom" class="mint-loadmore-bottom">
		          	<span v-show="bottomStatus !== 'loading'" :class="{ 'is-rotate': bottomStatus === 'drop' }" ref="statusBottom">{{statusBottom}}↑</span>
		          	<transition name="fade">
			          	<span v-show="bottomStatus === 'loading'">
			            	<mt-spinner type="snake"></mt-spinner>
			          	</span>
		          	</transition>
		        </div>
	      	</mt-loadmore>
	      	<transition name="fade">
	      		<span class="Prompt" v-show="PromptShow">暂无更多数据</span>
	      	</transition>
	    </div>
  	</div>
</template>

<script type="text/babel">
  	export default {
	    data() {
	      	return {
	      		listdata:"",//获取的总数据
		        list: [],	//上拉加载数据
		        refurbish:[],//刷新数据
		        allLoaded: false,
		        bottomStatus: '',
		        wrapperHeight: 0,
		        topStatus: '',
		        translate: 0,
		        statusTop:"下拉刷新",
		        statusBottom:"上拉加载",
		        PromptShow:false
	      	};
	    },
	    methods: {
	    	handleTopChange(status) {
		        this.topStatus = status;
//		        console.log(this.$refs.statusTop)
//		        if(this.$refs.statusTop.getAttribute("class")){
//		        	this.statusTop= "请放手刷新吧";
//		        }else{
//		        	this.statusTop = "下拉刷新";
//		        }
	      	},
	      	loadTop() {
	        	setTimeout(() => {
	        		var length = this.listdata.length;
	        		var y = 0;
	        		var w = 20;
                   	var p = this.listdata.length;
                   	if(p<w){
                   		w = p;
                   	}
		          	for (let i = 0; i < w; i++) {
		          		y=Math.floor(Math.random()*length);
		          		console.log(y)
		          		this.refurbish[i]=(this.listdata[y]);
		          	}
		          	this.list = this.refurbish;
		          	this.$refs.loadmore.onTopLoaded();
	        	}, 1000);
	      	},
	      	handleBottomChange(status) {
	        	this.bottomStatus = status;
//	        	if(this.$refs.statusBottom.getAttribute("class")){
//		        	this.statusBottom = "请放手加载吧";
//		        }else{
//		        	this.statusBottom = "下拉刷新";
//		        }
	      	},
	      	translateChange(translate) {
		        const translateNum = +translate;
		        this.translate = translateNum.toFixed(2);
		        console.log(this.translate)
	      	},
	      	loadBottom() {
		        setTimeout(() => {
		        	var length = this.listdata.length;
		          	var lastValue = this.list.length;
		          	var u = 0;
		          	if (lastValue <= length) {
		          		var x = 10;
		          		if(length-lastValue<10){
		          			x=length-lastValue;
		          			u = 1;
		          		};
			            for (let i = 0; i < x; i++) {
			              	this.list.push(this.listdata[lastValue + i]);
			            };
		          	}else{
		            	this.allLoaded = true;
		          	};
		          	if (lastValue == length && u == 1) {
		          		this.PromptShow = true;
			          	let times = setTimeout(()=>{
				        	this.PromptShow = false;
				        	clearTimeout(times);
				        },1500);
			        };
		          	this.$refs.loadmore.onBottomLoaded();
		        }, 1000);
	      	},
		},
	    created() {
	        this.$http.get('http://datainfo.duapp.com/shopdata/getGoods.php?callback=').then((response) => {
	                   	this.listdata = response.data;
	                   	this.listdata = this.listdata.substring(1,this.listdata.length-1);
	                   	this.listdata = JSON.parse(this.listdata);
	                   	var w = 5;
	                   	var p = this.listdata.length;
	                   	if(p<w){
	                   		w = p;
	                   	};
	                   	console.log(this.listdata)
	                   	for (let i = 0; i < w; i++) {
				        	this.list.push(this.listdata[i]);
				      	};
				      	this.$nextTick(function() {
					      	console.log(this.$refs.lists)
					      	this.$refs.lists.children[0].setAttribute("class","page-loadmore-listitem border-topbottom");
				      	});
	        		}, (response) => {
	                    console.log('error');
	        		});
	    },
	    mounted() {
	    	this.$nextTick(function() {
	      		this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top;
			});
	    }
  	};
</script>
<style lang="scss">
	@import "./common/css/border";
/*<style lang="scss" scoped>*/
	.mint-loadmore-top span {
	    -webkit-transition: .2s linear;
	    transition: .2s linear
	}
	.mint-loadmore-top span {
	    display: inline-block;
	    vertical-align: middle
	}
	.mint-loadmore-top span .mint-spinner-snake{
		border-top-color: rgb(12,204,200)!important;
	    border-left-color: rgb(12,204,200)!important;
	    border-bottom-color: rgb(12,204,200)!important;
	    height: 0.20rem!important;
    	width: 0.20rem!important;
	}
	/*.mint-loadmore-top span.is-rotate {
	    -webkit-transform: rotate(180deg);
	    transform: rotate(180deg)
	}*/
	.page-loadmore .mint-spinner {
	    display: inline-block;
	    vertical-align: middle
	}
	.page-loadmore-listitem {
	    /*border-bottom: 1px solid #eee*/
	}
	.page-loadmore-listitem {
	    height: auto;
	    line-height: 2.20rem;
	    text-align: center
	}
	.page-loadmore-listitem:last-child {
	}
	.page-loadmore-wrapper {
	    overflow: scroll
	}
	.mint-loadmore-bottom span {
	    display: inline-block;
	    -webkit-transition: .2s linear;
	    transition: .2s linear;
	    vertical-align: middle
	}
	.mint-loadmore-bottom span .mint-spinner-snake{
		border-top-color: rgb(12,204,200)!important;
	    border-left-color: rgb(12,204,200)!important;
	    border-bottom-color: rgb(12,204,200)!important;
	    height: 0.20rem!important;
    	width: 0.20rem!important;
	}
	/*.mint-loadmore-bottom span.is-rotate {
	    -webkit-transform: rotate(180deg);
	    transform: rotate(180deg)
	}*/
	.fade-enter-active {
	  	transition: all .6s ease;
	}
	.fade-leave-active {
	  	transition: all .6s ease;
	}
	.fade-enter, .fade-leave-active {
	  	/*transform: translateY(0.5rem);*/
	  	/*transform:rotate(360deg);*/
	  	opacity: 0;
	}
	.Prompt{
		position:fixed;
		left:0;
		bottom:0;
		display:inline-block;
		width:100%;
		height:0.5rem;
		line-height:0.5rem;
		text-align:center;
		color:#2C3E50;
		background:rgba(0,0,0,0.05);
	}
</style>



